// Code
code {
    font-family: $mono-family;
    text-rendering: optimizeLegibility;
    font-feature-settings: "calt" 1;
    font-variant-ligatures: normal;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    font-size: inherit;
  
    &.highlighter-rouge {
      padding: 1px 3px;
      position: relative;
      top: -1px;
      background-color: $dark-light;
      border-radius: 2px;
      border: 1px solid rgba(128,128,128,0.1);
    }
  }
  
  // Codeblock Theme
  pre.highlight, pre {
    margin: 0 -27px;
    @include media-query($on-mobile) {
      margin: 0 calc(51% - 51vw);
      padding-left: 20px;
    }
    border: 1px solid rgba(128,128,128,0.1);
    background-color: #1a1b21;
    border-radius: 2px;
    padding: 10px;
    display: block;
    overflow-x: auto;
  
    > code {
      width: 100%;
      max-width: 50rem;
      margin-left: auto;
      margin-right: auto;
      line-height: 1.5;
      display: block;
      border: 0;
    }
  }
  
  .highlight table td {
    padding: 5px;
  }
  
  .highlight table pre {
    margin: 0;
  }
  
